import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import { HeaderButton, List, Cell, Link, platform, CellButton, FormStatus, Header, SimpleCell, InfoRow, View, FixedLayout, Search, Separator, Tabs, TabsItem, IOS } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28MoneyRequestOutline from '@vkontakte/icons/dist/28/money_request_outline';
import Icon16Verified from '@vkontakte/icons/dist/16/verified';
import Icon28ListCheckOutline from '@vkontakte/icons/dist/28/list_check_outline';
import Icon28ListCircleFillGray from '@vkontakte/icons/dist/28/list_circle_fill_gray';
import Icon28MoneySendOutline from '@vkontakte/icons/dist/28/money_send_outline';
import Icon28Users from '@vkontakte/icons/dist/28/users';
import Icon28UserAddedOutline from '@vkontakte/icons/dist/28/user_added_outline';
import Icon28ListOutline from '@vkontakte/icons/dist/28/list_outline';
import Icon28PaperplaneOutline from '@vkontakte/icons/dist/28/paperplane_outline';
import Icon28MoneyCircleOutline from '@vkontakte/icons/dist/28/money_circle_outline';
import { Icon28DonateCircleFillYellow } from '@vkontakte/icons';
import { Icon28LightbulbCircleFillYellow } from '@vkontakte/icons';

import persik from '../img/persik.png';
import logo from '../img/logo.svg';
import './Persik.css';
import './Tochka.css';


const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id} centered={true}>
				<PanelHeader
			left={<PanelHeaderButton onClick={go} data-to="profile">
				{Home === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			
		</PanelHeader>

                <Div style={{marginTop:70}}>


</Div>

                        <Group description="   ,     .">
        <Header mode="secondary"></Header>
        <Cell before={<Icon28MoneyRequestOutline width={30} height={30} />}>    ?</Cell>
      </Group>
      <Group description="      .">
        <Cell before={<Icon28DonateCircleFillYellow width={30} height={30} />}>    <br/> ?</Cell>
      </Group>
      <Group description="   ' ', .">
        <Cell before={<Icon28LightbulbCircleFillYellow width={30} height={30} />}>    ?</Cell>
      </Group>
      
      <Group description="- (check list)      ,    ,    .">
        <Cell before={<Icon28ListCircleFillGray width={30} height={30} />}>   ?</Cell>
      </Group>
      
            <Group description="   ' '    .">
        <Cell before={<Icon28ListOutline width={30} height={30} />}>  ,  ?</Cell>
      </Group>
            <Group description="        .">
        <Cell before={<Icon28PaperplaneOutline width={30} height={30} />}>   , <br/>  ?</Cell>
      </Group>
            <Group description="            .">
        <Cell before={<Icon28MoneyCircleOutline width={30} height={30} />}>     <br/>  ?</Cell>
      </Group>
      <Group description="      ,             .">
        <Cell before={<Icon28ListCheckOutline width={30} height={30} />}> ,   </Cell>
      </Group>

            </Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
